/*=============================================
      ОБЩИЕ ПРАВИЛА
=============================================*/

.clearfix:after::after {
  display: block;
  content: "";
  clear: both;
}

.wrapper {
  max-width: 1276px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

html {
  font-size: 100%;
}

body {
  width: 100%;
  font-family: Rubik, Arial, sans-serif;
  font-size: 20px;
  line-height: 1.375;
  font-weight: 400;
  color: #111111;
}

p,
ul,
li,
figure,
figcaption {
  margin: 0;
  padding: 0;
}

.title-section {
  margin: 0 auto;
  text-align: center;
  font-family: Roboto, Arial, sans-serif;
  font-size: 42px;
  line-height: 49px;
  font-weight: 700;
  font-style: normal;
  color: #111111;
}

.btn-base {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #b7000e;
  border: none;
  font-family: Rubik, Arial, sans-serif;
  font-weight: 500;
  color: #ffffff;
  transition: background ease 0.5s;
  cursor: pointer;
}

.btn-base:hover {
  background: #ea0012;
}

.btn-base:active {
  background: #84000a;
}

.btn-call {
  padding: 0 22px 0 7px;
  border-radius: 4px;
  font-size: 15px;
  line-height: 18px;
}

.btn-cat {
  margin: 0 auto;
  padding: 20px 28px;
  border-radius: 6px;
  font-size: 17px;
  line-height: 20px;
}

.btn-cat img {
  margin-right: 10px;
}

.hide {
  display: none;
}

/*=============================================*/

/*=============================================
        ШАПКА
=============================================*/

.top-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: calc(100% - 38px);
  padding: 30px 0 28px 0;
}

.top-line__block {
  font-size: 17px;
  line-height: 25px;
  color: #aaaaaa;
}

.top-line__block1 > div,
.top-line__block4 > div {
  display: flex;
}

.top-line__social-box {
  margin-left: 12px;
}

.top-line__social-box .icon-viber.icon-viber--mob {
  display: none;
}

.top-line__tel {
  display: block;
  font-size: 20px;
  color: #111111;
}

.top-line__email {
  font-size: 17px;
  color: #b7000e;
}

.top-line__adress {
  font-style: normal;
}

.top-line__block4 {
  display: none;
}

.logo {
  display: block;
}

.logo__title {
  text-align: center;
  font-size: 15px;
  line-height: 18px;
  color: #111111;
}

.nav-line {
  position: relative;
  padding: 10px 0;
  background: #111111;
}

.nav-line__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 15px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}

.burger__icon {
  position: relative;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  border-radius: 3px;
}

.burger__icon::before {
  content: '';
  position: absolute;
  top: -6px;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  border-radius: 3px;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  content: '';
  position: absolute;
  top: 6px;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  border-radius: 3px;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon--active {
  background-color: transparent;
}

.burger__icon.burger__icon--active::before {
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(45deg);
}

.burger__icon.burger__icon--active::after {
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(-45deg);
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__burger {
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__burger p {
  display: none;
  font-size: 15px;
  line-height: 18px;
  font-weight: 700;
  color: #ffffff;
}

.nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
}

.nav__item {
  margin-left: 65px;
}

.nav__item:first-child {
  position: relative;
  margin-left: 12px;
}

.nav__item:first-child span {
  cursor: default;
}

.nav__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 22px;
  line-height: 26px;
  font-weight: 500;
  color: #ffffff;
  transition: color ease 0.5s;
}

.nav__link:hover {
  color: #ea0012;
}

.nav__link.active {
  color: #84000a;
}

.nav__link--drop {
  width: 100%;
  background: transparent;
  border: none;
}

.nav__link svg {
  display: none;
}

.nav__submenu {
  position: absolute;
  top: 100%;
  left: -10px;
  width: 265%;
  padding: 20px 10px 20px;
  background: #111111;
  list-style: none;
  transition: opacity 0.5s ease-in;
  height: auto;
  overflow: visible;
  opacity: 1;
}

.nav__submenu--visial {
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.1s ease-in;
}

.nav__subitem svg {
  display: none;
}

.nav__sublink {
  font-size: 1.17vw;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
}

.nav__sublink:hover {
  color: #ea0012;
}

.nav__sublink--disabled {
  color: #999;
  pointer-events: none;
  cursor: default;
}

.nav__sublink span:hover {
  cursor: pointer;
}

.nav__btn--submenu {
  display: none;
}

.nav__submenu2 {
  padding-left: 20px;
  background: #eeeeee;
  list-style-type: none;
  transition: opacity 0.1s ease-in;
  height: auto;
  overflow: visible;
  opacity: 1;
}

.nav__submenu2--visial {
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.1s ease-in;
}

