/*---------------------------------------------
  Секция head
---------------------------------------------*/

.head {
  width: 100%;
  background: url(../img/images/bg-head-hd.jpg) no-repeat;
  background-color: #eee;
  background-position: center 0;
  background-size: cover;
  text-align: center;
}

.head__wrap {
  position: relative;
  width: 100%;
  padding-bottom: 49.6%;
}

.head__wrap--active {
  z-index: -1;
}

.head__content {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 222px 0 531px 0;
}

h1 {
  max-width: 565px;
  margin: 0 auto 28px;
  text-align: center;
  font-size: 48px;
  line-height: 57px;
  font-weight: 500;
  color: #ffffff;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция slider
---------------------------------------------*/

.slider {
  position: relative;
  padding: 60px 0 50px;
}

.slider__arrows {
  position: absolute;
  top: 33%;
  left: 50%;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1536px;
  margin: 0 auto;
  padding: 0 20px;
  transform: translateX(-50%);
}

.slider__arrows button {
  display: inline-block;
  background: transparent;
  border: none;
  cursor: pointer;
}

.slider__arrows svg {
  width: 40px;
  height: 40px;
  fill: #b7000e;
}

.slider__item {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
}

.slider__item div {
  position: relative;
}

.slider__img {
  display: block;
  width: 100%;
  height: auto;
}

.slider__accia {
  position: absolute;
  left: 0;
  bottom: 0;
  display: inline-block;
  padding: 7px 45px 9px 27px;
  background: #b7000e;
  border-radius: 0 45px 0 0;
  font-family: Rubik, Arial, sans-serif;
  font-size: 17px;
  line-height: 140%;
  font-weight: 500;
  color: #ffffff;
  transition: background ease 0.5s;
}

.slider__accia:hover {
  background: #ea0012;
}

.slider__desc {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-top: 25px;
}

.slider a:hover h4 {
  color: #b7000e;
}

.slider__title {
  margin: 0 0 20px;
  font-size: 24px;
  line-height: 140%;
  font-weight: 400;
  color: #111111;
}

.slider__subtitle {
  display: inline-block;
  margin-top: auto;
  color: #aaaaaa;
}

.owl-carousel .owl-stage {
  display: flex;
  align-items: stretch;
}

/*Пагинация слайдера
------------------------------------------------*/

.owl-carousel .owl-dots {
  width: 100%;
  margin-top: 40px;
  margin-left: -15px;
  text-align: center;
}

.owl-carousel .owl-dots .owl-dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-left: 17px;
  background-color: #eeeeee;
  border-radius: 50%;
  transition: all ease 0.5s;
}

.owl-carousel .owl-dots .owl-dot.active {
  background-color: #b7000e;
  transform: scale(1.1);
}

