/*---------------------------------------------
  Секция catalog
---------------------------------------------*/

.catalog {
  padding: 77px 0 100px 0;
  background: url(../img/images/bg-catalog.jpg) no-repeat;
  background-size: cover;
}

.catalog__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: center;
  align-items: stretch;
  margin-top: 40px;
}

.card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.card__img {
  display: block;
  width: 100%;
  height: auto;
}

.card__desc {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px 24px 32px;
  background: #ffffff;
  border-radius: 0 0 10px 10px;
}

.card__title {
  margin: 0;
  text-align: center;
  font-size: 24px;
  line-height: 140%;
  font-weight: 400;
  color: #111111;
}

.card__title span {
  display: block;
  text-align: center;
  font-size: 14px;
  color: #aaaaaa;
}

.card a:hover h4 {
  font-weight: 500;
  color: #b7000e;
}

.card__price {
  margin-top: auto;
  text-align: center;
  line-height: 30px;
  font-weight: 500;
  color: #b7000e;
}

