/*roboto-700 - cyrillic*/
@font-face {
    font-family: Roboto;
    font-weight: 700;
    font-style: normal;
    src: local('Roboto Bold'),
 local('Roboto-Bold'),
        url('../fonts/Roboto/roboto-v20-cyrillic-700.woff2') format('woff2'),
        /* Chrome 26+, Opera 23+, Firefox 39+ */
        url('../fonts/Roboto/roboto-v20-cyrillic-700.woff') format('woff');

    /*Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+*/
}

/*rubik-regular - cyrillic*/
@font-face {
    font-family: Rubik;
    font-weight: 400;
    font-style: normal;
    src: local('Rubik'),
 local('Rubik-Regular'),
        url('../fonts/rubik/rubik-v9-cyrillic-regular.woff2') format('woff2'),
        /* Chrome 26+, Opera 23+, Firefox 39+ */
        url('../fonts/rubik/rubik-v9-cyrillic-regular.woff') format('woff');

    /*Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+*/
}

